<script setup>
import { ref, onMounted } from "vue";
import axios from "axios";

const username = ref("Loading...");
const firstName = ref("Loading...");
const lastName = ref("Loading...");
const hash = ref("");

const validateData = async (queryData) => {
  try {
    const response = await axios.post(
      "http://localhost:8000/validate",
      queryData
    );
    console.log("Validation response:", response.data);
  } catch (error) {
    if (error.response) {
      console.error("Validation failed:", error.response.data);
    } else {
      console.error("Request error:", error.message);
    }
  }
};

onMounted(async () => {
  if (window.Telegram && window.Telegram.WebApp) {
    window.Telegram.WebApp.ready();

    const user = window.Telegram.WebApp.initDataUnsafe?.user;

    if (user) {
      username.value = user.username || "No username";
      firstName.value = user.first_name || "No first name";
      lastName.value = user.last_name || "No last name";
      hash.value = user.hash;

      console.log(username.value);

      console.log(window.Telegram.WebApp.initData);

      const queryData = new URLSearchParams(window.Telegram.WebApp.initData);
      console.log(queryData);

      // Вызов функции для валидации данных с await
      await validateData(queryData);
    }
  } else {
    console.error("Telegram WebApp API не доступен.");
  }
});
</script>

<template>
  <header>
    <div class="container">
      <div class="header-content">
        <div class="username-wrapper">{{ username }} fff</div>
        <div class="settings-wrapper">Settings</div>
      </div>
    </div>
  </header>
</template>

